import React, { useState } from 'react'
import { Alert } from 'react-bootstrap'
import './Contact.css'

import api from '../../../services/api'


export default function Contact() {
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [message, setMessage] = useState('')
    const [show, setShow] = useState(false)
    const [ErroShow, setErroShow] = useState(false)

    async function handleSubmit(e) {
        e.preventDefault()

        await api.post('/send', {
            name,
            email,
            message
        })
            .then(() => setShow(true))
            .catch(() => setErroShow(true))
        setName('')
        setEmail('')
        setMessage('')
    }

    return (
        <div className="contact container" id="contact">
            <h3>VAMOS ENTRAR EM CONTATO ?</h3>
            <div className="box">
                    <Alert
                        variant="success"
                        show={show}
                        onClose={() => setShow(false)}
                        dismissible
                    >
                        {`Obrigado, seu contato foi enviado com Sucesso!`}
                    </Alert>
                    <Alert
                        variant="danger"
                        show={ErroShow}
                        onClose={() => setErroShow(false)}
                        dismissible
                    >
                        {`Erro no envio.`}
                    </Alert>
                <form>
                    <div className="form-group">
                        <label>Nome:</label>
                        <input
                            type="text"
                            className="form-control"
                            value={name}
                            onChange={e => setName(e.target.value)}
                        />
                    </div>
                    <div className="form-group">
                        <label>E-mail:</label>
                        <input
                            type="email"
                            className="form-control"
                            value={email}
                            onChange={e => setEmail(e.target.value)}
                        />
                    </div>
                    <div className="form-group">
                        <label>Mensagem:</label>
                        <textarea
                            className="form-control"
                            value={message}
                            onChange={e => setMessage(e.target.value)}
                        ></textarea>
                    </div>
                    <button
                        type="submit"
                        className="btn btn-outline btn-lg btn-block button"
                        onClick={handleSubmit}
                    >
                        Enviar
                        </button>
                </form>
                <div>
                </div>
            </div>
            <div className="row contact-phone d-flex justify-content-center">
                <div className="col-sm">
                    <div className="box-phone">
                        <div><i className="fa fa-3x fa-whatsapp"></i></div>
                        <div className="esp">Telefone</div>
                        <span>+55 61 98223-0101</span>
                    </div>
                </div>
                <div className="col-sm">
                    <div><i className="fa fa-3x fa-envelope"></i></div>
                    <div className="esp">E-mail</div>
                    <span>pedrook16@gmail.com</span>
                </div>
                <div className="col-sm">
                    <div><i className="fa fa-3x fa-street-view"></i></div>
                    <div className="esp">Localização</div>
                    <span>Brasília-DF, Brazil</span>
                </div>
            </div>
        </div>
    );
}


