import React, {useEffect, useState} from 'react'
import AnchorLink from 'react-anchor-link-smooth-scroll'
import './Banner.css'


export default function Banner() {

  const [skills, setSkills] = useState('PHP')
  
  useEffect(() => {
    setTimeout(() => {
      setSkills('MongoDB')        
    },6000)        
  },[])
  
  useEffect(() => {
    setTimeout(() => {
      setSkills('MySQL')        
    }, 25000)      
  }, [])

  useEffect(() => {
    setTimeout(() => {
      setSkills('NodeJS')        
    },35000)      
  }, [])
  useEffect(() => {
    setTimeout(() => {
      setSkills('ReactJS')        
    },45000)      
  }, [])
  useEffect(() => {
    setTimeout(() => {
      setSkills('Heroku')        
    },55000)      
  }, [])
  useEffect(() => {
    setTimeout(() => {
      setSkills('Jest')        
    },65000)      
  }, [])
  useEffect(() => {
    setTimeout(() => {
      setSkills('Karma')        
    },75000)      
  }, [])
  useEffect(() => {
    setTimeout(() => {
      setSkills('Jasmine')        
    },85000)      
  }, [])
  useEffect(() => {
    setTimeout(() => {
      setSkills('React Native')        
    },95000)      
  }, [])

  return (
    <div className="banner">       
          <div className="container content">        
              <h1 className="name">Pedro Henrique</h1>
               <span className="skills" style={{opacity: 0.8}}>{skills}</span><br></br>
                 <AnchorLink href="#hello"><i className="fa fa-5x  fa-angle-down"></i></AnchorLink>       
          </div>
        </div>
  )
}
