import React, { Component } from 'react'
import './About.css'


export default class About extends Component {
  render() {
    return (
      <div className="about container" id="about">
        <h3>SOBRE</h3>
        <div className="row">
          <p>
            Olá, meu nome é Pedro Henrique, sou programador júnior. Atualmente trabalho no Banco do Brasil na área de testes automizados, faço aplicações em NodeJS e React para uso interno do Banco. Iniciei minha trajetótia na programação a 2 anos como emprego, mas desde dos 12 anos programava por hobby, comecei com o PHP e migrei para o Javascript aonde estou desde então.
          </p>
        </div>
      </div>
    )
  }
}


